.card {
  max-width: 200px;
  height: 408px;
  transition-duration: .6s;
}

.card__img {
  text-align: center;
  margin-bottom: 37px;
}

.card__img img {
  width: 160px;
  height: 134px;
}

.card__title h2 {
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title p {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 12px;
  display: flex;
}

.card__review p {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price span {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__btn button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  cursor: pointer;
  transition-duration: .3s;
  transform: scale(1.2);
}

.card:hover .card__title h2 {
  color: #34568b;
}

.card:hover button {
  color: #00acdc;
  background-color: #fff;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav-logo {
  width: 40px;
  height: 40px;
}

.nav-bar {
  margin: 0;
}

.nav-items {
  align-items: center;
  height: 60px;
  list-style: none;
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  text-align: center;
  margin: 0 10px;
  line-height: 60px;
  position: relative;
}

ul li:first-child {
  margin-left: 0;
}

ul li:last-child {
  margin-right: 0;
}

a {
  color: #060b35;
  height: 100%;
  text-decoration: none;
  display: inline-block;
}

a:hover {
  color: #00acdc;
}

a:visited {
  color: #060b35;
}

li > .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-template-columns: repeat(1, 1fr);
  justify-content: space-between;
  align-items: center;
  gap: 46px 48px;
  max-width: 944px;
  margin: 0 auto;
  padding: 50px 40px;
  transition-duration: .3s;
  display: grid;
}

@media screen and (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }
}

@media screen and (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 1fr);
    display: grid;
  }
}

@media screen and (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 1fr);
    display: grid;
  }
}

body {
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__wrap {
  box-sizing: border-box;
  padding-inline: 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  height: 408px;
  padding-top: 32px;
  padding-bottom: 16px;
}
